.navigation .navigation-checkbox{display:none}.navigation .navigation-checkbox:checked ~ .navigation-background{-webkit-transform:scale(80);transform:scale(80)}.navigation .navigation-checkbox:checked ~ .navigation-nav{opacity:1;width:100%}.navigation .navigation-button{background-color:#fff;height:7rem;width:7rem;position:fixed;display:flex;justify-content:center;align-items:center;top:6rem;right:6rem;border-radius:50%;z-index:2000;box-shadow:0 1rem 3rem rgba(0,0,0,0.2)}.navigation .navigation-background{height:6rem;width:6rem;border-radius:50%;position:fixed;top:6.5rem;right:6.5rem;background-image:radial-gradient(#7ed56f, #28b485);z-index:1000;transition:-webkit-transform .4s;transition:transform .4s;transition:transform .4s, -webkit-transform .4s}.navigation-nav{height:100vh;position:fixed;top:0;right:0;z-index:1500;opacity:0;width:0;transition:all .4s}.navigation-nav .navigation-list{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);list-style:none;text-align:center}.navigation-nav .navigation-list .navigation-item{margin:2rem}.navigation-nav .navigation-list .navigation-item:hover{background-color:#fff;box-shadow:0 1rem 1rem rgba(0,0,0,0.2)}.navigation-nav .navigation-list .navigation-item:hover .navigation-link{color:#7ed56f;font-weight:400}.navigation-nav .navigation-list .navigation-item .navigation-link{text-decoration:none;font-size:3rem;font-weight:300;color:#fff;text-transform:uppercase}.navigation i{color:slategray}

